.navigation {
    position: sticky;
    top: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
    z-index: 10;
    background: #f5cac2;
    opacity: 90%;
  }
  
  .navigation ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .navigation li {
    display: inline-block;
    padding: 2%;
    margin: 0;
  }
  
  .navigation li a {
    color: #141850;
    text-align: center;
    text-decoration: none;
  }
  
  .navigation li a:hover {
    background-color: #303179;
    color: white;
  }


